
"use client";

import Link from "next/link";
import { LanguageSwitcher } from "@/components/language-switcher";
import { useLanguage } from "@/contexts/language-context";
import { Button } from "@/components/ui/button";
import { cn } from "@/lib/utils";
import { usePathname } from "next/navigation";
import { LockedInWorkLogo } from "@/components/icons/lockedinwork-logo";

export default function Header() {
  const { t } = useLanguage();
  const pathname = usePathname();

  const navItems = [
    { href: "/", labelKey: "navHome" },
    { href: "/demo", labelKey: "navDemo" },
    { href: "/book-appointment", labelKey: "navBook" },
  ];

  return (
    <header className="sticky top-0 z-50 w-full border-b border-border/40 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 max-w-screen-2xl items-center justify-between">
        <Link href="/" className="flex items-center space-x-2 text-primary hover:opacity-80 transition-opacity" aria-label={t('appName')}>
          <LockedInWorkLogo className="h-7 w-auto" /> {/* Adjusted height slightly */}
        </Link>
        <nav className="hidden md:flex items-center space-x-6 text-sm font-medium">
          {navItems.map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className={cn(
                "transition-colors hover:text-primary",
                pathname === item.href ? "text-primary" : "text-foreground/80"
              )}
            >
              {t(item.labelKey)}
            </Link>
          ))}
        </nav>
        <div className="flex items-center space-x-2">
          <LanguageSwitcher />
          {/* Mobile Menu Trigger (optional, can be added later) */}
        </div>
      </div>
    </header>
  );
}
